/* harmony default export */ __webpack_exports__["a"] = ({
  name: 'HelloWorld',
  props: ['isShowAudio'],
  data: function data() {
    return {
      picwidth: document.body.clientWidth - 40, //屏幕宽度
      imgwidth: '',
      audiow: '',
      isbgaudio: true,
      scrolled: false,
      isAndroid: '',
      isiOS: '',
      isqq: '',
      headerheight: 0,
      clickMusic: '',
      chushi: true,
      speed: 1, //初始速度
      speed1: 1, //
      speed2: 13, //
      speed3: 1,
      wujinghuoyu: '', //高度
      shouyi: {
        startX: 0,
        startY: 0,
        moveEndX: 0,
        moveEndY: 0,
        x: 0,
        y: 0,
        fangxiang: true

      }, //
      xishu: 1 //屏幕系数

    };
  },
  created: function created() {
    var self = this;
    var u = navigator.userAgent;
    self.isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
    self.isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
    self.isqq = u.match(/\sQQ/i) !== null; //是否QQ

    var p = 0,
        t = 0;

    $(window).scroll(function (e) {
      p = $(this).scrollTop();

      if (t <= p) {
        //下滚  
        self.fangxiang = true;
      } else {
        //上滚  
        self.fangxiang = false;
      }
      setTimeout(function () {
        t = p;
      }, 0);
    });

    self.$nextTick(function () {
      self.imgwidth = self.picwidth / 5 * 0.7;
      $('.pic').css('width', self.imgwidth);

      $('.page').click(function () {
        $("body").unbind('touchmove');
        $('.page').fadeOut(500);
        event.preventDefault();
      });
      document.addEventListener("WeixinJSBridgeReady", function () {
        if (self.isiOS) {

          //					self.audiow.play();
          //      	self.audiow.pause();
        }
      }, false);
      $('.pinch-zoom').each(function () {
        new RTP.PinchZoom($(this), {});
      });
      if (document.body.clientWidth < 375) {
        self.xishu = 0.8;
      } else if (document.body.clientWidth >= 375 && document.body.clientWidth < 414) {
        self.xishu = 1;
      } else {
        self.xishu = 1.05;
        $('.hello').css('height', '3700px');
      }
      if (self.isqq) {
        $('.shicha').css("top", -60 + "px");
        $('.allshicha').css("top", -10 + "px");
        $('.xsnrtitle').css('margin-bottom', '80px');
        $('.shanshuo').css('margin-top', '80px');
        $('.shanshuo1').css('margin-top', '20px');
        $('.hello').css('height', 'auto');
      }
    });
  },

  methods: {
    jump: function jump(num) {
      var self = this;
      self.audiodom.play();
      setTimeout(function () {
        self.$emit('listenToChildEvent', num);
      }, 100);
      // }
    },
    show: function show(num) {
      var self = this;
      self.audiodom.play();
      $("body").bind('touchmove', function (event) {
        event.preventDefault();
      }, false);
      if (num == 1) {
        $('.pagebg').attr('src', './static/banner/1.jpg');
      } else if (num == 2) {
        $('.pagebg').attr('src', './static/banner/2.jpg');
      } else if (num == 3) {
        $('.pagebg').attr('src', './static/banner/3.jpg');
      } else if (num == 4) {
        $('.pagebg').attr('src', './static/banner/4.jpg');
      } else if (num == 5) {
        $('.pagebg').attr('src', './static/banner/5.jpg');
      } else if (num == 6) {
        $('.pagebg').attr('src', './static/banner/moreadress.png');
      }
      setTimeout(function () {
        $('.page').fadeIn(500);
      }, 500);
    },
    handleScroll: function handleScroll() {
      this.scrolled = window.scrollY > 0;
      //  	$("body").unbind('touchmove');
      //  	console.log(window.scrollY )
      var self = this;

      var ju = window.scrollY * 0.02;
      var ju1 = window.scrollY * 0.1;
      var ju2 = window.scrollY * 0.1;
      var ju3 = window.scrollY * 0.011;
      if (!self.isqq) {
        $('.shicha').css("top", -ju1 + "px");
        $('.allshicha').css("top", -ju + "px");
        if (window.scrollY > 500 && window.scrollY < 960) {} else if (window.scrollY > 980 * self.xishu && window.scrollY < 1760 * self.xishu) {
          if (self.fangxiang) {
            self.speed1 = self.speed1 + 0.3;
          } else {
            self.speed1 = self.speed1 - 0.3;
          }
          if (self.speed1 > 50) {
            self.speed1 = 50;
          }
          if (self.speed1 < -30) {
            self.speed1 = -30;
          }
          $('.sc1').css("-webkit-transform", "translateY(" + -self.speed1 + "px" + ")");
          $('.sc1').css("transform", "translateY(" + -self.speed1 + "px" + ")");
          //  		$('.sc1').css("top",-self.speed1+"px");
        } else if (window.scrollY > 1200 * self.xishu && window.scrollY < 2300 * self.xishu) {
          if (self.fangxiang) {
            self.speed = self.speed + 0.5;
          } else {
            self.speed = self.speed - 0.5;
          }
          if (self.speed > 40) {
            self.speed = 40;
          }
          if (self.speed < -20) {
            self.speed = -20;
          }
          $('.sc').css("-webkit-transform", "translateY(" + -self.speed + "px" + ")");
          $('.sc').css("transform", "translateY(" + -self.speed + "px" + ")");
          //  		$('.sc').css("top",-self.speed+"px");
        } else if (window.scrollY > 2163 * self.xishu && window.scrollY < 2645 * self.xishu) {
          if (self.fangxiang) {
            self.speed2 = self.speed2 + 0.5;
          } else {
            self.speed2 = self.speed2 - 0.5;
          }
          if (self.speed2 > 50) {
            self.speed2 = 50;
          }
          if (self.speed2 < -30) {
            self.speed2 = -30;
          }
          $('.sc3').css("-webkit-transform", "translateY(" + -self.speed2 + "px" + ")");
          $('.sc3').css("transform", "translateY(" + -self.speed2 + "px" + ")");
        } else if (window.scrollY > 2500 * self.xishu && window.scrollY < 3600 * self.xishu) {
          if (self.fangxiang) {
            self.speed3 = self.speed3 + 0.5;
          } else {
            self.speed3 = self.speed3 - 0.5;
          }
          if (self.speed3 > 30) {
            self.speed3 = 30;
          }
          if (self.speed3 < -10) {
            self.speed3 = -10;
          }
          $('.sc4').css("-webkit-transform", "translateY(" + -self.speed3 + "px" + ")");
          $('.sc4').css("transform", "translateY(" + -self.speed3 + "px" + ")");
          //  		$('.sc4').css("top",-self.speed3+"px");
        }
      }
      if (window.scrollY > 680) {
        $('.backnav').fadeIn(500);
        $('.nav').fadeIn(500);
      } else {
        $('.nav').fadeOut(500);
        $('.backnav').fadeOut(500);
      }
    },
    back: function back() {
      $('body,html').animate({ scrollTop: 0 }, 200);
    }
  },
  mounted: function mounted() {
    var self = this;
    //		self.audiow = document.getElementById('Music')
    self.audiodom = document.getElementById('clickMusic');
    setTimeout(function () {
      window.sessionStorage.setItem('headerheight', $('.bigbanner').height());
      self.headerheight = $('.bigbanner').height();
    }, 2000);
    window.addEventListener('scroll', this.handleScroll);
  },
  updated: function updated() {},

  watch: {
    isShowAudio: function isShowAudio() {
      var self = this;
      if (self.isShowAudio) {
        //      self.audiow.play();
      }
    }
  }
});

